import React from 'react'
import { FormLabel, Box } from '@mui/material';
import { DataGrid } from '@mui/x-data-grid';
import result_infos from '../config/result_info';

export default function Showres(props) {
  // console.log("res", props.result)
  return (
    <Box
      component="form"
      display="flex"
      gap={4}
      p={2}
      sx={{
        border: '1px solid #aeaeae',
        borderRadius: '10px',
        width: '100%'
      }}
      noValidate
      autoComplete="off"
    >
      <div className='output_container'>
        <FormLabel component="legend">XGBoost Prediction Result</FormLabel>
        {Object.keys(props.result).map(key => (
          <div>
            <p>{result_infos.filter((e) => e.Treatment === key)[0].Show_name}: {Math.round(parseFloat(props.result[key]) * 100) / 100}</p>
          </div>
        ))}
      </div>
    </Box>
  )
}
